/* stylelint-disable font-family-name-quotes */
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

:root {
  --header-width: 100vw;
  --header-height: 60px;
  --blue-color: #00acdc;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  background: #fff;
  box-shadow: 0 2px 4px rgb(0, 0, 0, 0.05);
}

.logo {
  display: block;
  height: 40px;
  width: 40px;
}

.logo__image {
  height: 100%;
  width: 100%;
}

.nav__list {
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  position: relative;
  display: flex;
  align-items: center;
  height: 60px;
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  font-weight: 500;
  white-space: nowrap;
  text-decoration: none;
  text-transform: uppercase;
  color: #000;
  transition: color 0.3s;
}

.nav__link:hover {
  color: var(--blue-color);
}

.nav__link:target {
  color: var(--blue-color);
}

.is-active {
  color: var(--blue-color);
}

.is-active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 4px;
  border-radius: 2px;
  background-color: var(--blue-color);
}
